<template>
  <div>
    <h2>{{ symbol }} Chart</h2>
    <canvas ref="chartCanvas"></canvas>
  </div>
</template>

<script>
import axios from 'axios';
import { Chart, LineController, LineElement, PointElement, LinearScale, Title, CategoryScale } from 'chart.js';

Chart.register(LineController, LineElement, PointElement, LinearScale, Title, CategoryScale);

export default {
  name: 'StockChart',
  data() {
    return {
      symbol: 'AAPL',
      chart: null,
    };
  },
  mounted() {
    this.loadData();
  },
  methods: {
    async loadData() {
      const API_KEY = 'FD4P53J29BMD6SES';
      const url = `https://www.alphavantage.co/query?function=TIME_SERIES_INTRADAY&symbol=${this.symbol}&interval=1min&apikey=${API_KEY}`;

      const response = await axios.get(url);
      const values = response.data.values;

      const labels = values.map((v) => v.datetime).reverse();
      const prices = values.map((v) => parseFloat(v.close)).reverse();

      this.chart = new Chart(this.$refs.chartCanvas, {
        type: 'line',
        data: {
          labels,
          datasets: [{
            label: `${this.symbol} Preis`,
            data: prices,
            borderColor: 'blue',
            tension: 0.3,
          }],
        },
        options: {
          responsive: true,
          scales: {
            y: {
              beginAtZero: false,
            },
          },
        },
      });
    }
  }
};
</script>
